/*
 * Theme: https://coolors.co/2d3142-4f5d75-bfc0c0-ffffff-ef8354
 */

*, *::before, *::after {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

*::selection {
    background-color: rgba(0, 0, 0, 0.15);
}

[v-cloak] {
    display: none;
}

html, body, .container {
    height: 100%;
    width: 100%;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.6;
    color: #2d3142;
    background-color: #fff;
    position: relative;
}

a {
    text-decoration: none;
    color: #ef8354;
}

a:hover {
    text-decoration: underline;
}

p {
    margin-bottom: 20px;
}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.content {
    max-width: 700px;
    width: 100%;
    text-align: center;
    margin: 0 auto;
    padding: 20px;
}

.brand {
    font-weight: 500;
    font-size: 72px;
    margin-bottom: 10px;
    color: #ef8354;
}

.form-group,
.form-label,
.form-input,
.form-error {
    display: block;
    width: 100%;
    text-align: left;
}

.form-group {
    &.has-error .form-input {
        border-color: #cc5357;
    }

    &:not(:last-child) {
        margin-bottom: 10px;
    }
}

.form-error {
    color: #cc5357;
    font-weight: 500;
}

.form-label {
    margin-bottom: 10px;
}

.form-message {
    margin-bottom: 20px;
}

.form-input {
    background-color: #fff;
    color: #2d3142;
    border: 1px solid #bfc0c0;
    border-radius: 3px;
    padding: 15px;
    font-size: 20px;

    &:focus {
        outline: 0;
        border: 1px solid #333;
    }
}

.form-together {
    display: flex;
    margin-bottom: 10px;

    .form-group:not(:last-child) {
        margin-right: 20px;
    }
}

.button {
    background-color: #ef8354;
    color: #fff;
    border: 1px solid #cc7048;
    padding: 10px 15px;
    border-radius: 3px;
    font-size: 20px;
    cursor: pointer;
}

.button:focus {
    outline: 0;
    border-color: #995436;
}

@media screen and (max-width: 600px) {
    .form-together {
        display: block;
    }

    .form-together .form-group {
        margin-right: 0;
        margin-bottom: 20px;
        display: block;
    }

    .form-together .button {
        margin-left: 0;
    }

    .button {
        width: 100%;
    }
}

@media screen and (max-height: 350px) {
    body {
        margin-bottom: 20px;
    }

    .container {
        display: block;
    }
}
